        async checkScriptExistence() {
            if (!this.hasValidServerId) {
                this.$message.error('ID');
                return;
            }

            try {
                this.debugging = true;
                this.debugInfo = '...\n';

                const commands = [
                    'ls -la /root/Nftato.sh',
                    'ls -la /root/Nftato.sh',
                    'find /root -name "*.sh" | grep -i Nftato',
                    'find / -name "*.sh" -type f -not -path "*/\\.*" | grep -i Nftato 2>/dev/null'
                ];

                for (const command of commands) {
                    this.debugInfo += `\n: ${command}\n`;
                    const response = await this.$store.dispatch('servers/executeCommand', {
                        serverId: this.serverId,
                        command
                    });

                    if (response && response.success) {
                        const stdout = response.data?.stdout || '';
                        const stderr = response.data?.stderr || '';

                        this.debugInfo += `:\n${stdout}\n`;
                        if (stderr) {
                            this.debugInfo += `:\n${stderr}\n`;
                        }

                        if (stdout && (stdout.includes('Nftato.sh') || stdout.includes('Nftato.sh'))) {
                            this.debugInfo += '\n\n';
                            this.debugInfo += '\n';
                            this.$message.warning('');
                            break;
                        }
                    } else {
                        this.debugInfo += `: ${response?.error || ''}\n`;
                    }
                }

                this.debugInfo += '\n...\n';
                const execResponse = await this.$store.dispatch('servers/executeCommand', {
                    serverId: this.serverId,
                    command: 'cd /root && (./Nftato.sh --help || ./Nftato.sh --help || echo "")'
                });

                if (execResponse && execResponse.success) {
                    const stdout = execResponse.data?.stdout || '';
                    this.debugInfo += `:\n${stdout}\n`;

                    if (stdout.includes('') || stdout.includes('nftables')) {
                        this.debugInfo += '\n\n';
                        this.debugInfo += '\n';
                        this.$message.success('');
                    }
                } else {
                    this.debugInfo += `: ${execResponse?.error || ''}\n`;
                }
            } catch (error) {
                this.debugInfo += `\n: ${error.message}\n`;
                this.$message.error(`: ${error.message}`);
            } finally {
                this.debugging = false;
            }
        },
